import { SEARCH_PRODUCT, SORT_BY_NAME, SORT_BY_PRICE } from '../actions/types';

const filterReducersDefaultState = {
    text: '',
    sortBy: '',
    price: ''
};


export default (state = filterReducersDefaultState, action) => {
    switch (action.type) {
        case SEARCH_PRODUCT:
            return {
                ...state,
                text: action.text
            };
        case SORT_BY_NAME:
            return {
                ...state,
                sortBy: 'name'
            };
        case SORT_BY_PRICE:
            return {
                ...state,
                sortBy: 'price'
            };
        default:
            return state;
    }
}